<template>
  <div>
    <Navbar :pageTitle="currentPageName" :pages="children" />
    <router-view></router-view>
  </div>
</template>
<script>
import Navbar from "@/components/Navbar.vue";
import PageModel from "@/models/pageModel";
import authMixin from "../mixins/authMixin";
import { EventBus } from "@/bus";

export default {
  name: "UserView",
  components: {
    Navbar,
  },
  data: () => {
    return {
      currentPageName: "",
      children: [
        new PageModel("Dasbor", "mdi-view-dashboard", {
          name: "UserDashboard",
        }),
        new PageModel("Profil", "mdi-account", {
          name: "UserProfile",
        }),
        new PageModel("Beli Paket", "mdi-text-box-plus", {
          name: "UserPurchaseTryout",
        }),
        new PageModel("Statistik", "mdi-chart-line", {
          name: "UserStatistic",
        }),
      ],
    };
  },
  created() {
    let userData = this.getUserData;

    if (userData.role !== "USER") {
      this.$router.push({name : "Login"});
    } else {
      EventBus.$on("onPageChanged", (pageTitle) => {
        this.currentPageName = pageTitle;
      });
    }
  },
  mixins: [authMixin],
};
</script>